@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'Atlas Grotesk';
  src: url(./assets/fonts/atlas_grotesk/commercial-type-2202-ARTXTP-web/AtlasGrotesk-Regular-Web.woff) format("woff");
  font-weight:  normal;
  font-style:  normal;
}

@font-face {
  font-family: 'Atlas Grotesk';
  src: url(./assets/fonts/atlas_grotesk/commercial-type-2202-ARTXTP-web/AtlasGrotesk-Bold-Web.woff) format("woff");
  font-weight:  bold;
  font-style:  normal;
}

@font-face {
  font-family: 'Atlas Grotesk';
  src: url(./assets/fonts/atlas_grotesk/commercial-type-2202-ARTXTP-web/AtlasGrotesk-Medium-Web.woff) format("woff");
  font-weight:  600;
  font-style:  normal;
}

@font-face {
  font-family: 'Gascogne';
  src:  url(./assets/fonts/gascogne/gascogne/TypeShop Collection - GascogneTS-DemiBold.ttf) format("truetype");
}

body {
  margin: 0;
  font-family: 'Atlas Grotesk', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight:  normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

svg * {
  transition: fill .5s;
}

table, th, td {
  font-size:  .75rem;
  border: 1px solid black;
  border-collapse: collapse;
}

td {
  padding:  .25rem;
}